import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  withRouter,
} from "react-router-dom";

import "./buttons_header.css";
import { useCheats } from "hooks/useCheats";

const Buttons_Header = ({ game }) => {
  // const [currentGame, setcurrentGame] = useState(game)
  const { currentgame, setCurrentgame, getCategories, categories } =
    useCheats(false);

  useEffect(() => {
    getCategories();
    setCurrentgame(game);
  }, [game]);

  return (
    <div className="buttons_header">
      <Button
        className={
          currentgame == "all" ? "button_header active_button" : "button_header"
        }
        variant="outlined"
        to="/cheats/all"
        component={Link}
      >
        All Games
      </Button>
      {categories
        ? categories.map((c, index) =>
            c.active ? (
              <Button
                className={
                  currentgame == c.url
                    ? "button_header active_button"
                    : "button_header"
                }
                variant="outlined"
                to={"/cheats/" + c.url}
                component={Link}
                key={index}
              >
                {c.name}
              </Button>
            ) : null
          )
        : "no hay categorias"}
    </div>
  );
};

export default Buttons_Header;
