import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Pagination from '@material-ui/lab/Pagination';


export default function AIRPagination({totalCheats,cheatsPerPage, paginate }) {

  const totalpages= Math.ceil(totalCheats / cheatsPerPage);

  return (
    <div className="pagination_div">
      {
        totalCheats <1
        ?null
        :<Pagination count={totalpages} color="secondary" onChange={(e,value)=> paginate(value)}/>
      }
    </div>
  );
}
